h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

ul,
li {
  list-style: none;
}

button,
a {
  text-decoration: none;
  color: inherit;
  border: none;
}

input {
  border: none;
  outline: none;
}

textarea {
  border: none;
  outline: none;
}

p {
  margin: 0;
  padding: 0;
}

aside {
  overflow: hidden;
}
